@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300,400,500,700&display=swap');
* {
    box-sizing: border-box;
    margin: 0;
  }
  
  .container{
  
  }
  html,
  body,
  #root {
    margin: 0;
    padding: 0;
    background-color: white;
    max-width: 100%;
  }

  
  body {
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial,
      sans-serif;
  }
 

  .padding-block {
    text-align: center;
    padding: 5%;
    display: flex;
    text-align: center;
    font-size: 3vmax;
    justify-content: center;
    align-items: center;
    background-color: #efefef;
    color: #333;
    overflow-x: hidden;
    overflow-y: hidden;
  }
  .padding-block-below {
    display: flex;
    text-align: center;
    font-size: 5vmax;
    justify-content: center;
    align-items: center;
    background-color: #efefef;
    color: #333;
  }

  .normal-text{
      font-weight: 400;
      padding-top: 10%;
      font-size: 50%;
      font-family: Poppins;
  }
  .normal-text-end{
    line-height: 1.6;
    font-size: 3vmax;
    background-color: #efefef;
    margin-bottom: 10%;
    font-weight: 400;
    padding-top: 0%;
    font-size: 50%;
    font-family: Poppins;
}
  .seventy{
    font-size: 70%; 
  }
  .fifty{
    font-size: 50%; 
  }
  .scroll-carousel {
   padding: 10%;
    background-color: #efefef;
    
  }
  
  .sticky-box {
    position: sticky;
    top: 10%;
    overflow: hidden;
  }
  
  .transform-box {
    display: flex;
    will-change: transform;
  }
  
  .box {
    flex-shrink: 0;
    /* need to hardcode box sizes, else images loading throws off calculations */
    /* could be a job for imagesLoaded */
    width: 70rem;
    height: 80vh;
    margin: 0 2rem;
  }
  
  .box--narrower {
    width: 20rem;
  }
  
  .img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }


  transition {
    background-color: #efefef;
    font-size: 140%;
    margin-left: 10%;
    display:inline-block;
    vertical-align: top;
    width: 30vw;
    height: 20vh;
    overflow-x:hidden;
    white-space:nowrap;
  }
  
  transition:first-of-type {    /* For increasing performance 
                         ID/Class should've been used. 
                         For a small demo 
                         it's okaish for now */
    margin-right: 50%;
    animation: showup 7s infinite;

  }
  
  transition:last-of-type {
    width:0px;
    animation: reveal 7s infinite;
  }
  
  transition:last-of-type span {
    margin-left:-400px;
    animation: slidein 7s infinite;
  }
  
  @keyframes showup {
      0% {opacity:0;}
      20% {opacity:1;}
      80% {opacity:1;}
      100% {opacity:0;}
  }
  
  @keyframes slidein {
      0% { margin-left:-400px; }
      20% { margin-left:-400px; }
      35% { margin-left:0px; }
      100% { margin-left:0px; }
  }
  
  @keyframes reveal {
      0% {opacity:0;width:0px;}
      20% {opacity:1;width:0px;}
      30% {width:600px;}
      80% {opacity:1;}
      100% {opacity:0;width:600px;}
  }

  .scroll {
    margin-top: 10%;
  margin-left: 50%;
  background-color:#efefef;
	width: 60px;
	height: 60px;
	border: 2px solid #333;
	border-radius: 50%;
	position: relative;
	animation: down 1.5s infinite;
	-webkit-animation: down 1.5s infinite;
	&::before {
    background-color:#efefef;
		content: '';
		position: absolute;
		top: 15px;
		left: 18px;
		width: 18px;
		height: 18px;
		border-left: 2px solid #333;
  	border-bottom: 2px solid #333;
		transform: rotate(-45deg);
	}
}

@keyframes down {
  
	0% {
		transform: translate(0);
	}
	20% {
		transform: translateY(15px);
	}
	40% {
		transform: translate(0);
	}
}

@-webkit-keyframes down {
	0% {
		transform: translate(0);
	}
	20% {
		transform: translateY(15px);
	}
	40% {
		transform: translate(0);
	}
}



@media only screen and (max-device-width: 480px) {
            transition {
              font-size: 120%;
              margin-left: 5%;
              display:inline-block;
              vertical-align: top;
              width: 25vw;
              height: 20vh;
              overflow:hidden;
              white-space:nowrap;
            overflow-x: hidden;
            }
            transition:first-of-type {    /* For increasing performance 
              ID/Class should've been used. 
              For a small demo 
              it's okaish for now */
          margin-right: 30%;
          animation: showup 7s infinite;
          overflow-x: hidden;
            }
          transition:last-of-type {
          width:0px;
          animation: reveal 7s infinite;
          overflow-x: hidden;
          }

          transition:last-of-type span {
          margin-left:-200px;
          animation: slidein 7s infinite;
          overflow-x: hidden;
          }
          @keyframes slidein {
            0% { margin-left:-110px; margin-right: 5%}
            20% { margin-left:-110px; margin-right: 5%}
            35% { margin-left:-210px; }
            100% { margin-left:-210px; }
        }
}

