@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300,400,500,700&display=swap');

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, caption {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
}

.h1{
    text-align: center;
    line-height: 1.6;
    padding: 100px;
    font-family: constitution;
    text-align: center;
    font-size: 50px;
}


h2{
    overflow-y: hidden;
    padding: 20px;
    line-height:1.6;
    text-align: center;
    font-family: constitution;


}
.p{
    padding: 20px;
    text-align: center;
}
.container1{
    letter-spacing: 2px;
    line-height: 1.6;
    /* background-image: url('../Assets/flagside.jpg'); */
     background-size: 100%;
     background-color: #efefef;
font-family: "Poppins",-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
text-align: center;
padding-left: 15%;
padding-right: 15%;
font-weight: 400;
font-size: 25px;
}

@font-face {
    font-family: 'constitution';
    font-style: normal;
    font-weight: 300;
    src: local('constitituion'), local('OpenSans'),
         url('../Assets/zai_USDeclaration.ttf') format('truetype')
  }


  @media only screen and (max-device-width: 480px) {

    .h1{
        text-align: center;
        line-height: 1.6;
        padding: 100px;
        font-family: constitution;
        text-align: center;
        font-size: 30px;
    }

    .container1{
        letter-spacing: 2px;
        line-height: 1.6;
        /* background-image: url('../Assets/flagside.jpg'); */
         background-size: 100%;
         background-color: #efefef;
    font-family: "Poppins",-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
    padding-left: 15%;
    padding-right: 15%;
    font-weight: 400;
    font-size: 15px;
    }
    
  }
