@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300,400,500,700&display=swap');

html {
   body{
     padding: 0;
     margin: 0;
     border: 0;
    outline: 0;
     font-family: "Poppins",-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   }
}

.top-left{
      margin-left: 10%;
      position: absolute;
      width: 56px;
      height: 1px;
      background: #efefef;
      top: 4%;
      left: -68px;
      width:auto;
      height:auto;
      max-width:100%;
      max-height:100vh;
}

button{
  margin: 10px;
  background: none;
  border: none;
  font-size: 12px;
  padding: 0;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.7px;
  display: inline-block;
  align-items: center;
  position: relative;
  &:focus {
    outline: none;
  }
  .arrow-icon{
    
  }
}

@media only screen and (max-device-width: 480px) {
  .top-left{
    margin-left: 20%;
    position: absolute;
    width: 40px;
    height: 1px;
    background: #efefef;
    top: 4%;
    left: -68px;
    width:auto;
    height:auto;
    max-width:100%;
    max-height:100vh;
}
